<template>
  <div class="Transfer">
    <div class="TransferBox" v-for="(item, index) in dataFormList" :key="index">
      <div class="TransferHeader">
        <img
          v-if="!item.CustPictureUrl"
          class="TransferHeaderImgA"
          draggable="false"
          src="@/assets/image/InstallHeadPortrait.png"
          alt=""
        />
        <img
          v-if="item.CustPictureUrl"
          class="TransferHeaderImgA"
          draggable="false"
          :src="item.CustPictureUrl"
          alt=""
        />
        <div class="t_box">
          <span class="t">
            {{ item.Name }}
          </span>
          <span class="ts" v-if="statusList[index]">
            購買機型：P0231
          </span>
          <div class="tm" v-if="statusList[index]">
            安装地址：台中市西區敦化北路2號
          </div>
        </div>
        <img
          v-if="item.IsFromOpportunity"
          class="TransferHeaderImgB"
          draggable="false"
          src="@/assets/image/commercialOpportunity.png"
          alt=""
        />
        <img
          @click="
            clickStatus(true, index);
            GetCustomerRemark(item.CustId, index);
          "
          v-if="!statusList[index]"
          class="TransferHeaderImgRight"
          draggable="false"
          src="@/assets/image/right.png"
          alt=""
        />
        <img
          @click="clickStatus(false, index)"
          v-if="statusList[index]"
          class="TransferHeaderImgBottom"
          draggable="false"
          src="@/assets/image/bottom.png"
          alt=""
        />
      </div>
      <div class="TransferContent" v-if="statusList[index]">
        <h2 class="zp">
          <img src="@/assets/image/icon_gifts.png" />贈品：陶板屋禮券乙張
        </h2>
        <div class="TransferContentBox">
          <p>訂單日期：2020年05月22日</p>
          <p>訂單編號：20202002020202020</p>
          <p>聯絡電話：0981231243</p>
        </div>
        <div class="d-flex">
          <span class="price">$19800元/1台</span>
          <div class="d-flex btnBox">
            <div class="cancel" @click="alertModel()">取消</div>
            <router-link to="/InstallReplacement/Replacement" class="btn"
              >開始安裝</router-link
            >
          </div>
        </div>
        <!--        <div @click="toTransferDetails(item.CustId, item.Mobile)">-->
        <!--          查看詳情-->
        <!--        </div>-->
      </div>
    </div>

    <div class="reveal-overlay" id="alertModel">
      <div class="alert-model">
        <div class="alert-content">
          <h2>未安裝·原因</h2>
          <div class="box">
            <label class="css-radio">
              <input type="radio" name="group" /><span></span> 環境不符
            </label>
            <label class="css-radio">
              <input type="radio" name="group" checked="" /><span></span>
              其他原因
            </label>
            <textarea placeholder="請填寫原因"></textarea>
            <span class="tips">未安裝原因不得為空</span>
          </div>
          <div class="d-flex">
            <div class="cancel" @click="alertModelHid()">取消</div>
            <div class="send" @click="alertModelHid()">送出</div>
          </div>
        </div>
      </div>
    </div>

    <!-- 空组件 -->
    <noContent v-if="dataFormListStatus" />
    <!-- 加载中组件 -->
    <Loading :show="loadingShow" />
  </div>
</template>

<script>
import noContent from "@/components/noContent";
import Loading from "@/components/Loading";
import {
  POST_GetMyCustomers,
  POST_GetCustomerRemark,
  POST_GetCustomerByDistributtion,
} from "@/utils/api";
export default {
  name: "Transfer",
  components: {
    noContent,
    Loading,
  },
  data() {
    return {
      loadingShow: false,
      dataFormListStatus: false,
      statusList: [],
      dataFormList: [
        {
          headerImgUrl: require("@/assets/image/InstallHeadPortrait.png"),
          Name: "王阿英",
          iconImgUrl: require("@/assets/image/commercialOpportunity.png"),
          location: "台中市西屯區南海路2號2樓",
          phone: "0923452345",
          content:
            "2019年12月購入新的淨水器機器家中現在有三台櫻花淨水器等設備。2019年12月購入新的淨水器機器家中現在有三台櫻花淨水器等設備。",
        },
        {
          headerImgUrl: require("@/assets/image/InstallHeadPortrait.png"),
          Name: "陳大可",
          iconImgUrl: "",
          location: "台中市西屯區南海路2號2樓",
          phone: "0923452345",
          content:
            "2019年12月購入新的淨水器機器家中現在有三台櫻花淨水器等設備。2019年12月購入新的淨水器機器家中現在有三台櫻花淨水器等設備。",
        },
        {
          headerImgUrl: require("@/assets/image/InstallHeadPortrait.png"),
          Name: "张樱花",
          iconImgUrl: require("@/assets/image/commercialOpportunity.png"),
          location: "台中市西屯區南海路2號2樓",
          phone: "0923452345",
          content:
            "2019年12月購入新的淨水器機器家中現在有三台櫻花淨水器等設備。2019年12月購入新的淨水器機器家中現在有三台櫻花淨水器等設備。",
        },
      ],
    };
  },
  created() {
    // this.GetMyCustomers();
  },
  methods: {
    // 获取客户资料
    GetMyCustomers() {
      this.loadingShow = true;
      if (this.$route.query.CustPhone) {
        var UofId = {
          CustPhone: this.$route.query.CustPhone,
          DistributionUofId: sessionStorage.getItem("DistributionUofId"),
        };
        // 搜索API
        POST_GetCustomerByDistributtion(UofId).then((response) => {
          // 请求返回状态判断
          if (response.State) {
            this.dataFormList.push(response.Data);
            for (var i = 0; i < this.dataFormList.length; i++) {
              this.statusList.push(false);
            }
            this.dataFormListStatus = false;
          } else {
            this.dataFormListStatus = true;
          }
          // 判断列表没有数据是显示空组件
          if (this.dataFormList.length == 0) {
            this.dataFormListStatus = true;
          } else {
            this.dataFormListStatus = false;
          }
          this.loadingShow = false;
        });
      } else {
        var UofIds = {
          DistributionUofId: sessionStorage.getItem("DistributionUofId"),
        };
        // 列表请求API
        POST_GetMyCustomers(UofIds).then((response) => {
          // 请求返回状态判断
          if (response.State) {
            this.dataFormList = response.Data;
            for (var i = 0; i < this.dataFormList.length; i++) {
              this.statusList.push(false);
            }
            this.dataFormListStatus = false;
          } else {
            this.dataFormListStatus = true;
          }
          // 判断列表没有数据是显示空组件
          if (this.dataFormList.length == 0) {
            this.dataFormListStatus = true;
          } else {
            this.dataFormListStatus = false;
          }
          this.loadingShow = false;
        });
      }
    },
    // 获取备注
    GetCustomerRemark(CustId, index) {
      var UofIds = {
        CustId: CustId,
      };
      POST_GetCustomerRemark(UofIds).then((response) => {
        this.dataFormList[index].Remark = response.Data.Remark;
        this.$forceUpdate();
      });
    },
    clickStatus: function(item, index) {
      this.statusList.splice(index, 1, item);
      event.stopPropagation();
    },
    // 跳转到客户详情
    toTransferDetails(id, CustPhone) {
      this.$router.push(
        "/CustomerService/Transfer/TransferDetails?id=" +
          id +
          "&CustPhone=" +
          CustPhone
      );
    },
    alertModel() {
      document.getElementById("alertModel").classList.add("md-show");
    },
    alertModelHid() {
      document.getElementById("alertModel").classList.remove("md-show");
    },
  },
};
</script>

<style lang="scss" scoped>
.Transfer {
  padding-right: 5.33%;
  padding-top: 24px;
  .reveal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99999;
    opacity: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    outline: 0;
    background: rgba(0, 0, 0, 0.5);
    -webkit-pointer-events: none;
    -moz-pointer-events: none;
    -ms-pointer-events: none;
    -o-pointer-events: none;
    pointer-events: none;

    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    transition: all 0.3s;
    * {
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
    }
    .alert-model {
      min-height: calc(100% - 3.5rem);
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
      align-items: center;
      padding: 0 40px;
    }
    .alert-model .alert-content {
      padding: 30px 20px;
      border-radius: 24px;
      opacity: 0;
      -webkit-transform: scale(0.7);
      -moz-transform: scale(0.7);
      -ms-transform: scale(0.7);
      transform: scale(0.7);
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      transition: all 0.3s;
      text-align: center;
      margin: 0 auto;
      width: 100%;
      max-width: 280px;
    }
    .alert-content {
      background: rgba(255, 255, 255, 1);
      border-radius: 15px;
      h2 {
        font-size: 16px;
        font-weight: 500;
        color: rgba(61, 61, 61, 1);
        line-height: 20px;
        margin-bottom: 26px;
      }
      .css-radio {
        display: block;
        margin-bottom: 10px;
        font-size: 14px;
        color: #3d3d3d;
        input {
          padding: 0;
          margin: 4px 0 0;
          margin-top: 1px \9;
          line-height: normal;
          position: absolute;
          opacity: 0;
        }
        input + span {
          width: 14px;
          height: 14px;
          background-color: #f7f7f7;
          border: 1px solid #e7e7e7;
          border-radius: 50%;
          position: relative;
          display: inline-block;
          margin-top: -2px;
          margin-right: 3px;
          vertical-align: middle;
        }
        input + span:after {
          top: -1px;
          right: -1px;
          bottom: -1px;
          left: -1px;
          background-color: #fff;
          border-radius: 50%;
          opacity: 0;
          -webkit-transition: opacity 0.2s ease-out;
          transition: opacity 0.2s ease-out;
          position: absolute;
          content: "";
        }
        input:checked + span {
          border-color: #007bff;
          background-color: #007bff;
        }
        input:checked + span:after {
          background-repeat: round;
          background-image: url("data:image/png;base64,");
          opacity: 1;
        }
      }
      textarea {
        -webkit-appearance: none;
        display: block;
        height: auto;
        padding: 10px;
        width: 100%;
        line-height: 1.42857143;
        color: #bdbdbd;
        background-color: #f9f9fa;
        background-image: none;
        font-size: 12px;
        border: 1px solid #f9f9fa;
        border-radius: 4px;
      }
      textarea::-webkit-input-placeholder {
        color: #bdbdbd;
      }
      textarea::-moz-placeholder {
        color: #bdbdbd;
      }
      textarea::-ms-input-placeholder {
        color: #bdbdbd;
      }
      .tips {
        color: #fe603b;
        margin: 13px auto;
        display: block;
      }
      .d-flex {
        display: flex;
        justify-content: space-between;
      }
      .cancel {
        background: rgba(229, 239, 255, 1);
        color: #1c76fe;
        border-radius: 6px;
        font-size: 15px;
        line-height: 40px;
        width: calc(50% - 9px);
        height: 40px;
      }
      .send {
        color: #ffffff;
        font-size: 15px;
        background: rgba(28, 118, 254, 1);
        box-shadow: 0px 12px 30px 0px rgba(28, 118, 254, 0.15);
        border-radius: 6px;
        line-height: 40px;
        width: calc(50% - 9px);
        height: 40px;
      }
    }
  }
  .reveal-overlay.md-show {
    opacity: 1;
    -webkit-pointer-events: auto;
    -moz-pointer-events: auto;
    -ms-pointer-events: auto;
    -o-pointer-events: auto;
    pointer-events: auto;
    .alert-model .alert-content {
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      transition: all 0.3s;
      -webkit-transform: scale(1.2);
      -moz-transform: scale(1.2);
      -ms-transform: scale(1.2);
      transform: scale(1.2);
    }
  }
  .reveal-overlay.md-show .alert-model .alert-content {
    opacity: 1;
  }
  .TransferBox {
    background: rgba(255, 255, 255, 1);
    border-radius: 8px;
    padding: 21px 5.37%;
    margin-bottom: 10px;
    .TransferHeader {
      display: flex;
      align-items: center;
      .TransferHeaderImgA {
        border-radius: 50%;
        width: 30px;
        height: 30px;
      }
      .t_box {
        flex: 1;
        .t {
          font-size: 16px;
          font-weight: 500;
          color: rgba(61, 61, 61, 1);
          line-height: 22px;
        }
        .ts {
          font-size: 12px;
          font-weight: 500;
          color: rgba(61, 61, 61, 1);
          line-height: 17px;
        }
        .tm {
          font-size: 12px;
          font-weight: 500;
          color: rgba(189, 189, 189, 1);
          line-height: 17px;
          margin: 0 2.13%;
        }
      }
      span {
        font-size: 16px;
        font-weight: 500;
        color: rgba(61, 61, 61, 1);
        line-height: 22px;
        margin: 2.13%;
      }
      .TransferHeaderImgB {
        width: 62px;
        height: 17px;
      }
      .TransferHeaderImgRight {
        width: 9px;
        height: 16px;
        margin-left: auto;
      }
      .TransferHeaderImgBottom {
        width: 15px;
        height: 8px;
        margin-left: auto;
      }
    }
    .TransferContent {
      padding-left: calc(2.13% + 30px);
      margin-top: 8px;
      .zp {
        font-size: 12px;
        font-weight: 500;
        color: rgba(61, 61, 61, 1);
        line-height: 17px;
        padding-bottom: 10px;
        margin-bottom: 10px;
        border-bottom: 1px solid #eeedef;
        display: flex;
        align-items: center;
        img {
          margin-right: 3px;
        }
      }
      .TransferContentBox {
        margin-bottom: 10px;
      }
      .d-flex {
        display: flex;
        align-items: center;
        align-content: center;
        flex-wrap: wrap;
        .price {
          font-size: 14px;
          font-weight: 500;
          color: #fe603b;
          line-height: 20px;
        }
        .btnBox {
          margin-left: auto;
          .btn {
            margin-left: 14px;
          }
        }
      }
      h2 {
        font-size: 14px;
        font-weight: 500;
        color: rgba(61, 61, 61, 1);
        line-height: 20px;
      }
      p {
        font-size: 12px;
        font-weight: 500;
        color: rgba(189, 189, 189, 1);
        line-height: 18px;
      }
      .btn {
        margin-left: auto;
        text-align: center;
        background: linear-gradient(
          180deg,
          rgba(28, 118, 254, 1) 0%,
          rgba(28, 118, 254, 1) 100%
        );
        box-shadow: 0px 3px 12px 0px rgba(28, 118, 254, 0.3);
        border-radius: 6px;
        font-size: 14px;
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        line-height: 30px;
        width: 86px;
        height: 30px;
      }
      .cancel {
        font-size: 12px;
        font-weight: 500;
        color: rgba(189, 189, 189, 1);
        background: transparent;
        width: auto;
        height: auto;
      }
    }
  }
}
</style>
