<template>
  <div class="InstallImage">
    <div class="InstallImageBox">
      <h2>安裝後</h2>
      <div class="InstallImageBoxUploading wrap">
        <!-- <div class="newInputBox">
          <div class="t">原水水壓</div>
          <input type="text" v-model="WaterPressure" placeholder="請填寫原水水壓"  />
          <div class="d">kg/cm²</div>
        </div> -->
        <div class="newInputBox">
          <div class="t">原水TDS</div>
          <input type="text" v-model="TDS" placeholder="請填寫原水TDS" />
          <div class="d">PPM</div>
        </div>
        <div class="newInputBox">
          <div class="t">餘氯</div>
          <input type="text" v-model="Chlorine" placeholder="請填寫餘氯" />
        </div>
        <div class="newInputBox">
          <div class="t">原水PH值</div>
          <input type="text" v-model="PH" placeholder="請填寫原水PH值" />
        </div>
      </div>
      <h2>廚下空間照<small>Upload photos</small></h2>
      <div class="InstallImageBoxImage">
        <div class="listbox">
          <div class="list">
            <van-uploader
              v-model="fileList"
              @delete="deleteImg1"
              :after-read="beforeRead1"
              :max-count="1"
              :preview-full-image="false"
              @click-preview="openBigImg1"
            >
              <img src="@/assets/image/uploading.png" alt="" />
            </van-uploader>
            <div class="txt">{{ imgListTitle }}</div>
          </div>
          <div class="list">
            <van-uploader
              v-model="fileList2"
              @delete="deleteImg2"
              :after-read="beforeRead2"
              :max-count="1"
              :preview-full-image="false"
              @click-preview="openBigImg2"
            >
              <img src="@/assets/image/uploading.png" alt="" />
            </van-uploader>
            <div class="txt">{{ imgList2Title }}</div>
          </div>
          <div class="list">
            <van-uploader
              v-model="fileList3"
              @delete="deleteImg3"
              :after-read="beforeRead3"
              :max-count="1"
              :preview-full-image="false"
              @click-preview="openBigImg3"
            >
              <img src="@/assets/image/uploading.png" alt="" />
            </van-uploader>
            <div class="txt">{{ imgList3Title }}</div>
          </div>
        </div>
      </div>
      <p class="vanFieldTitle" v-show="titleStatus">此欄不得為空</p>
      <h2>掃描機體QR Code<small>Scan QR Code</small></h2>
      <div class="InstallImageBoxUploading">
        <van-field v-model="QRcode" placeholder="請輸入製造號碼" />
        <van-uploader
          :after-read="beforeReadCode"
          class="InstallImageBoxUploadingFlie"
        >
          <span>上傳QR Code</span>
        </van-uploader>
      </div>
      <p class="vanFieldTitle" v-show="titleStatuss">此欄不得為空</p>
      <!-- <p class="vanFieldTitle" v-show="titleStatuA">濾心製號不合法</p> -->
      <h2>實收金額<small>Price</small></h2>
      <div class="InstallImageBoxUploadings">
        <van-field v-model="Price" type="number" placeholder="請輸入實收金額" />
      </div>
      <p class="vanFieldTitle" v-show="PriceStatuss">此欄不得為空</p>
      <div class="InstallImageBoxBottom" @click="nextStep()">下一步</div>
    </div>
    <!-- 删除弹窗 -->
    <van-popup
      v-model="deleteShow"
      class="deleteShowVanPopup"
      :close-on-click-overlay="false"
    >
      <img src="@/assets/image/deleteImg.png" alt="" />
      <h2>確定刪除</h2>
      <div>
        <span @click="cancel(deleteNum)">取消</span>
        <em @click="confirm()">確定</em>
      </div>
    </van-popup>
    <!-- 处理中弹窗 -->
    <van-popup v-model="inHandShow" class="vanPopupinHand">
      <img draggable="false" src="@/assets/image/inHand.png" alt="" />
      <h2>處理中，請稍候...</h2>
    </van-popup>
    <!-- 失败弹窗 -->
    <van-popup v-model="failedShow" class="vanPopupfailed">
      <img draggable="false" src="@/assets/image/failed.png" alt="" />
      <h2>辨識失敗</h2>
      <h3>請再次嘗試，或洽總公司處理</h3>
      <div @click="failedShow = false">我知道了</div>
    </van-popup>

    <van-popup v-model="beDefeatedShow" class="beDefeatedShowStyle">
      <div class="img-box">
        <van-icon
          class="img-box-icon"
          @click="beDefeatedShow = false"
          size="30"
          name="cross"
        />
        <img :src="bigImg" alt="" />
      </div>
      <h2>{{ tit }}</h2>
    </van-popup>

    <SubmitLoading :show="LoadingStates" />
  </div>
</template>

<script>
import {
  POST_CreateWaterInstallImage,
  POST_GetMFNO,
  POST_CreateWaterInstallInit,
} from "@/utils/api";
import SubmitLoading from "@/components/SubmitLoading";
export default {
  name: "InstallImage",
  components: {
    SubmitLoading,
  },
  data() {
    return {
      LoadingStates: false,
      WaterPressure: "",
      TDS: "",
      Chlorine: "",
      PH: "",
      titleStatus: false,
      beDefeatedShow: false,
      titleStatuss: false,
      PriceStatuss: false,
      titleStatuA: false,
      deleteShow: false,
      inHandShow: false,
      failedShow: false,
      QRcode: "",
      Price: "",
      files: "",
      fileList: [],
      fileList2: [],
      fileList3: [],
      fileFormData: "",
      fileFormDataList: [],
      fileImg: "",
      bigImg: "",
      tit: "",
      deleteNum: "",
      imgListTitle: "進水閥開關處",
      imgList2Title: "機體燈號",
      imgList3Title: "廚下安裝後照片",
      imgList: "",
      file_switch: "",
      file_light: "",
      file_complete: "",
      InstallId: "",
    };
  },
  watch: {
    value: function() {
      if (this.value) {
        this.titleStatuss = false;
      }
    },
  },
  mounted() {
    this.CreateWaterInstallInit();
  },
  methods: {
    CreateWaterInstallInit() {
      var fd = {
        DistributionUofId: sessionStorage.getItem("DistributionUofId"),
      };
      POST_CreateWaterInstallInit(fd).then((response) => {
        this.InstallId = response.Data;
      });
    },
    openBigImg1(file) {
      this.beDefeatedShow = true;
      this.bigImg = file.content;
      this.tit = this.imgListTitle;
    },
    openBigImg2(file) {
      this.beDefeatedShow = true;
      this.bigImg = file.content;
      this.tit = this.imgList2Title;
    },
    openBigImg3(file) {
      this.beDefeatedShow = true;
      this.bigImg = file.content;
      this.tit = this.imgList3Title;
    },
    // 图片1上传
    async beforeRead1(file) {
      // this.file_switch = file.file;
      // this.file_switch = this.$api.compressImg(file.file)
      this.file_switch = await this.$api.compressImg(file.file);
    },
    // 图片2上传
    async beforeRead2(file) {
      // this.file_light = file.file;
      // this.file_light = this.$api.compressImg(file.file)
      this.file_light = await this.$api.compressImg(file.file);
    },
    // 图片3上传
    async beforeRead3(file) {
      // this.file_complete = file.file;
      // this.file_complete = this.$api.compressImg(file.file)
      this.file_complete = await this.$api.compressImg(file.file);
    },
    beforeReadCode(file) {
      let UofIds = new FormData();
      UofIds.append("file", file.file);
      this.inHandShow = true;
      POST_GetMFNO(UofIds).then(
        (response) => {
          this.QRcode = response.Data;
          this.inHandShow = false;
        },
        // eslint-disable-next-line no-unused-vars
        (err) => {
          this.inHandShow = false;
          this.failedShow = true;
        }
      );
    },

    // 点击删除时执行函数
    deleteImg1(item) {
      this.deleteShow = true;
      this.files = item;
      this.file_switch = "";
      this.deleteNum = 1;
    },
    // 点击删除时执行函数
    deleteImg2(item) {
      this.deleteShow = true;
      this.files = item;
      this.file_light = "";
      this.deleteNum = 2;
    },
    // 点击删除时执行函数
    deleteImg3(item) {
      this.deleteShow = true;
      this.files = item;
      this.file_complete = "";
      this.deleteNum = 3;
    },
    // 關閉遮罩層
    // imgclose() {
    //   if (this.deleteNum == 1) {
    //     if (!this.fileList.length) this.fileList.push(this.files);
    //     this.file_switch = this.files.file;
    //   } else if (this.deleteNum == 2) {
    //     if (!this.fileList2.length) this.fileList2.push(this.files);
    //     this.file_light = this.files.file;
    //   } else if (this.deleteNum == 3) {
    //     if (!this.fileList3.length) this.fileList3.push(this.files);
    //     this.file_complete = this.files.file;
    //   }
    // },
    // 取消删除
    cancel() {
      this.deleteShow = false;
      if (this.deleteNum == 1) {
        if (!this.fileList.length) this.fileList.push(this.files);
        this.file_switch = this.files.file;
      } else if (this.deleteNum == 2) {
        if (!this.fileList2.length) this.fileList2.push(this.files);
        this.file_light = this.files.file;
      } else if (this.deleteNum == 3) {
        if (!this.fileList3.length) this.fileList3.push(this.files);
        this.file_complete = this.files.file;
      }
    },
    // 确认删除
    confirm() {
      this.deleteShow = false;
    },
    // 下一步
    nextStep() {
      if (
        this.file_switch == "" ||
        this.file_light == "" ||
        this.file_complete == ""
      ) {
        this.titleStatus = true;
      } else {
        this.titleStatus = false;
      }
      if (this.Price == "") {
        this.PriceStatuss = true;
      } else {
        this.PriceStatuss = false;
      }
      if (this.QRcode == "") {
        this.titleStatuss = true;
        // this.titleStatuA = false;
      } else {
        this.titleStatuss = false;
        // var numReg = /^1BE/;
        // if (!numReg.test(this.QRcode)) {
        //   this.titleStatuA = true;
        //   return false;
        // }
        // if (this.QRcode.length > 14 || this.QRcode.length < 13) {
        //   this.titleStatuA = true;
        //   return false;
        // }
      }
      if (
        this.file_switch &&
        this.file_light &&
        this.file_complete &&
        this.QRcode &&
        this.Price
      ) {
        this.LoadingStates = true;
        this.titleStatuss = false;
        this.titleStatus = false;
        this.PriceStatuss = false;
        this.fileFormData = new FormData();
        this.fileFormData.append("file_switch", this.file_switch);
        this.fileFormData.append("file_light", this.file_light);
        this.fileFormData.append("file_complete", this.file_complete);
        this.fileFormData.append("InstallId", this.InstallId);
        POST_CreateWaterInstallImage(this.fileFormData).then((response) => {
          this.LoadingStates = false;
          if (response.State == false) {
            this.$notify({ type: "danger", message: response.Data });
            return false;
          }
          this.$router.push({
            name: "Signature",
            query: {
              id: this.InstallId,
              name: this.$route.query.name,
              Mfno: this.QRcode,
              Price: this.Price,
              type: "install",
              WaterPressure: this.WaterPressure,
              TDS: this.TDS,
              Chlorine: this.Chlorine,
              PH: this.PH,
            },
          });
        });
      } else {
        if (!this.file_switch) {
          this.$notify({ type: "danger", message: "第一張圖不能為空" });
        } else if (!this.file_light) {
          this.$notify({ type: "danger", message: "第二張圖不能為空" });
        } else if (!this.file_complete) {
          this.$notify({ type: "danger", message: "第三張圖不能為空" });
        } else if (!this.QRcode) {
          this.$notify({ type: "danger", message: "製造編號不能為空" });
        } else if (!this.Price) {
          this.$notify({ type: "danger", message: "價格不能為空" });
        }
        // this.$notify({ type: 'danger', message: "必填項不能為空" });
      }
    },
  },
};
</script>

<style lang="scss">
.InstallImage {
  .van-overlay {
    background: #000;
    z-index: 99999;
  }
}
</style>
<style lang="scss">
.van-popup--center {
  overflow: inherit;
  z-index: 99999 !important;
  .img-box {
    max-height: 375px;
    overflow: hidden;
    margin: 0 -50%;
    position: relative;
    .img-box-icon {
      position: absolute;
      right: 0;
      top: 0;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  .img-box + h2 {
    font-size: 15px;
    font-weight: 500;
    color: hsl(0, 0%, 100%);
    line-height: 21px;
    position: fixed;
    bottom: -43px;
    padding: 0 20px;
    margin-left: -50%;
  }
}
.InstallImage {
  width: 100%;
  .InstallImageBox {
    // padding: 0 5.33% 0;
    padding-top: 20px;
    .newInputBox {
      display: inline-block;
      width: 100%;
      background: hsl(240, 9%, 98%);
      border-radius: 4px;
      font-size: 16px;
      font-weight: 500;
      color: hsl(0, 0%, 74%);
      display: flex;
      align-items: center;
      padding: 11px 16px;
      margin-bottom: 10px;
      font-size: 13px;
      font-weight: 500;
      color: rgba(61, 61, 61, 1);
      line-height: 18px;
      input {
        display: block;
        box-sizing: border-box;
        flex: 1;
        width: 100%;
        margin-left: 14px;
        padding: 0;
        color: hsl(240, 1%, 20%);
        line-height: inherit;
        text-align: left;
        background-color: hsla(0, 0%, 0%, 0);
        border: 0;
        resize: none;
      }
      input::-webkit-input-placeholder {
        color: #bdbdbd;
      }
      input::-moz-placeholder {
        color: #bdbdbd;
      }
      input::-ms-input-placeholder {
        color: #bdbdbd;
      }
    }
    h2 {
      font-size: 14px;
      font-weight: 500;
      color: rgba(61, 61, 61, 1);
      line-height: 25px;
      small {
        font-size: 65%;
        padding-left: 8px;
      }
    }
    h3 {
      font-size: 12px;
      font-weight: 500;
      color: rgba(134, 134, 134, 1);
      line-height: 17px;
    }
    .InstallImageBoxImage {
      margin-top: 12px;
      margin-bottom: 16px;
      padding: 20px 5.97%;
      background: rgba(255, 255, 255, 1);
      border-radius: 8px;
      .listbox {
        display: flex;
        justify-content: space-between;
        .list {
          width: calc((100% / 3) - 11px);
          text-align: center;
        }
        .txt {
          font-size: 12px;
          font-weight: 500;
          color: rgba(189, 189, 189, 1);
          line-height: 17px;
          margin-top: 8px;
        }
      }
      .hide {
        opacity: 0;
        height: 0;
        width: 0;
        position: absolute;
      }
      p {
        font-size: 12px;
        font-weight: 500;
        color: rgba(254, 96, 59, 1);
        line-height: 17px;
        padding-left: 5px;
        padding-top: 5px;
      }
    }
    .InstallImageBoxUploading {
      display: flex;
      margin-top: 12px;
      margin-bottom: 16px;
      padding: 14px;
      height: 50px;
      background: rgba(255, 255, 255, 1);
      border-radius: 8px;
      font-size: 13px;
      .van-cell {
        display: inline-block;
        width: 60%;
        height: 50px;
        background: rgba(249, 249, 250, 1);
        border-radius: 4px;
        font-size: 13px;
        font-weight: 500;
        color: rgba(189, 189, 189, 1);
      }
      .InstallImageBoxUploadingFlie {
        width: 40%;
        height: 50px;
        background: rgba(249, 249, 250, 1);
        border-radius: 4px;
        font-size: 16px;
        font-weight: 500;
        color: rgba(28, 118, 254, 1);
        line-height: 44px;
        text-decoration: underline;
        .van-uploader__input-wrapper {
          font-size: 13px;
        }
      }
    }
    .InstallImageBoxUploading.wrap {
      flex-wrap: wrap;
      height: auto;
    }
    .InstallImageBoxUploadings {
      display: flex;
      margin-top: 12px;
      margin-bottom: 16px;
      padding: 14px;
      height: 50px;
      background: rgba(255, 255, 255, 1);
      border-radius: 8px;
      .van-cell {
        display: inline-block;
        width: 100%;
        height: 50px;
        background: rgba(249, 249, 250, 1);
        border-radius: 4px;
        font-size: 13px;
        font-weight: 500;
        color: rgba(189, 189, 189, 1);
      }
    }
    .vanFieldTitle {
      margin-top: -12px;
      margin-bottom: 16px;
      padding-left: 14px;
      font-size: 12px;
      font-weight: 500;
      color: rgba(254, 96, 59, 1);
      line-height: 17px;
    }
    .InstallImageBoxBottom {
      margin: 44px 0;
      height: 50px;
      background: linear-gradient(
        180deg,
        rgba(28, 118, 254, 1) 0%,
        rgba(28, 118, 254, 1) 100%
      );
      box-shadow: 0px 10px 40px 0px rgba(28, 118, 254, 0.3);
      border-radius: 12px;
      font-size: 16px;
      font-weight: 500;
      color: rgba(255, 255, 255, 1);
      line-height: 50px;
      text-align: center;
    }
  }
  .deleteShowVanPopup {
    padding: 40px 25px;
    width: 230px;
    height: 180px;
    background: rgba(255, 255, 255, 1);
    border-radius: 15px;
    text-align: center;
    h2 {
      margin-top: 26px;
      margin-bottom: 30px;
      font-size: 15px;
      font-weight: 500;
      color: rgba(61, 61, 61, 1);
      line-height: 20px;
    }
    div {
      display: flex;
      justify-content: space-between;
      span {
        display: inline-block;
        width: 106px;
        height: 40px;
        background: rgba(229, 239, 255, 1);
        border-radius: 6px;
        font-size: 15px;
        font-weight: 500;
        color: rgba(28, 118, 254, 1);
        line-height: 40px;
      }
      em {
        width: 106px;
        height: 40px;
        background: rgba(28, 118, 254, 1);
        box-shadow: 0px 12px 30px 0px rgba(28, 118, 254, 0.15);
        border-radius: 6px;
        font-size: 15px;
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        line-height: 40px;
      }
    }
  }
  .vanPopupinHand {
    width: 206px;
    padding: 30px 0;
    height: 104px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 14px 60px 0px rgba(0, 0, 0, 0.05);
    border-radius: 15px;
    text-align: center;
    img {
      width: 60px;
      height: 60px;
    }
    h2 {
      font-size: 16px;
      font-weight: 500;
      color: rgba(134, 134, 134, 1);
      line-height: 20px;
      margin-top: 22px;
    }
  }
  .vanPopupfailed {
    padding: 40px 0;
    width: 280px;
    height: 210px;
    background: rgba(255, 255, 255, 1);
    border-radius: 15px;
    text-align: center;
    img {
      width: 60px;
      height: 60px;
    }
    h2 {
      margin-top: 26px;
      margin-bottom: 4px;
      font-size: 15px;
      font-weight: 500;
      color: rgba(61, 61, 61, 1);
      line-height: 20px;
    }
    h3 {
      font-size: 13px;
      font-weight: 500;
      color: rgba(134, 134, 134, 1);
      line-height: 20px;
    }
    div {
      width: 150px;
      height: 40px;
      background: rgba(28, 118, 254, 1);
      box-shadow: 0px 12px 30px 0px rgba(28, 118, 254, 0.15);
      border-radius: 6px;
      font-size: 15px;
      font-weight: 500;
      color: rgba(255, 255, 255, 1);
      line-height: 40px;
      margin: 35px auto 0;
    }
  }
}
</style>
